body {
  margin: 0;
  font-size: 16px;
  font-family: "Roboto", sans-serif;
  color: #232323;
  background: #f0beb6;
}

.title {
  font-family: "DM Serif Text", serif;
  text-align: center;
}

.wrapper {
  max-inline-size: 43.75rem;
  margin: 0 auto;
  padding-inline: 1rem;
  box-sizing: border-box;
}

.header {
  padding-block: 1rem;
  background: #47344b;
  color: #F5F5F5;
}
.header__logo {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
.header__name {
  text-transform: uppercase;
  font-family: "Roboto", sans-serif;
  font-size: 0.875rem;
}
@media (min-width: 1024px) {
  .header {
    grid-column: 1/-1;
    grid-row: 1;
    padding-block: 2rem;
  }
  .header__content {
    max-inline-size: none;
    padding-inline: 3rem;
    margin: 0 auto;
  }
  .header__name {
    font-size: 1rem;
  }
}

.form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding-block: 2rem;
}
.form__campo {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}
.form__input, .form__textarea {
  padding: 0.5rem 0.5rem;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
  border-radius: 0.5rem;
  border: none;
}
.form__input:focus, .form__textarea:focus {
  outline: #47344b 1px solid;
}
.form__buttons {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-block-start: 2rem;
}
.form__buttons > * {
  flex: 1;
}
.form__buttonReset {
  padding: 0.625rem 0.5rem;
  border-radius: 0.5rem;
  color: #47344b;
  background: #F5F5F5;
  border: 1px solid #47344b;
  font-weight: 700;
  font-family: "Roboto", sans-serif;
  font-size: 1rem;
  cursor: pointer;
  position: relative;
}
.form__buttonSend {
  padding: 0.625rem 0.5rem;
  border-radius: 0.5rem;
  color: #F5F5F5;
  background: #47344b;
  border: 1px solid #47344b;
  font-weight: 700;
  font-family: "Roboto", sans-serif;
  font-size: 1rem;
  cursor: pointer;
}
.form__buttonSend:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
.form__buttonSend:disabled:hover::before {
  content: "sisi";
  display: block;
}
.form__error {
  background: rgb(216, 59, 59);
  color: white;
  margin: 0;
  padding: 0.5rem;
}
@media (min-width: 768px) {
  .form__buttonReset, .form__buttonSend {
    padding-block: 1rem;
    font-size: 1.125rem;
  }
}

@media (min-width: 1024px) {
  .layout {
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-template-rows: auto 1fr;
    block-size: 100vh;
    background: url("../img/perrito.png");
    background-position: left bottom;
    background-size: 35% 90%;
    background-repeat: no-repeat;
    background-color: #47344b;
  }
}

.main {
  grid-column: 2;
  grid-row: 2;
  background: #f0beb6;
}